import Vue from 'vue'
import Router from 'vue-router'
// import login from '@/views/login/login'
// import home from '@/views/home/home'
// import layout from '@/views/layout/index'
// import userData from '@/views/userInfo/userData'
// import user from '@/views/user/user'
// import system from '@/views/system/system'
// import role from '@/views/system/role/role'
// import department from '@/views/system/department/department'
// import dictionaries from '@/views/system/dictionaries/dictionaries'
// import menu from '@/views/system/menu/menu'
// import signIn from '@/views/system/signIn/signIn'
// import business from '@/views/system/business/business'
// import monitor from '@/views/system/monitor/monitor'
// import notice from '@/views/system/notice/notice'
// import statistics from '@/views/statistics/statistics'
// import vHistory from '@/views/statistics/vHistory/vHistory'
// import iForm from '@/views/statistics/iForm/iForm'
// import eLink from '@/views/statistics/eLink/eLink'
// import model from '@/views/model/model'
// import mSubmit from '@/views/model/mSubmit/mSubmit'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: 'login',
      component: () =>
      import('@/views/login/login')
    },
    {
      path: '/login',
      name: 'login',
      component: () =>
      import('@/views/login/login'),
      meta: {
        title: '登录'
      }
    },
    {
      path: '/home',
      component: () =>
      import('@/views/layout/index'),
      children: [
        { path: '/home',
          name: 'home',
          component: () =>
          import('@/views/home/home'),
          meta: {
            title: '通知'
          }
        },
        { path: '/userData',
          name: 'userData',
          component: () =>
          import('@/views/userInfo/userData'),
          meta: {
            title: '个人中心'
          }
        },
        { path: '/statistics',
          name: 'statistics',
          component: () =>
          import('@/views/statistics/statistics'),
          children: [
            { path: '/vHistory',
              name: 'vHistory',
              component: () =>
              import('@/views/statistics/vHistory/vHistory'),
              meta: {
                title: '访问记录'
              }
            },
            { path: '/iForm',
              name: 'iForm',
              component: () =>
              import('@/views/statistics/iForm/iForm'),
              meta: {
                title: '意向表单'
              }
            },
            { path: '/eLink',
              name: 'eLink',
              component: () =>
              import('@/views/statistics/eLink/eLink'),
              meta: {
                title: '引擎链接'
              }
            }
          ]
        },
        { path: '/model',
          name: 'model',
          component: () =>
          import('@/views/model/model'),
          children: [
            { path: '/mSubmit',
              name: 'mSubmit',
              component: () =>
              import('@/views/model/mSubmit/mSubmit'),
              meta: {
                title: '模型提交'
              }
            }
          ]
        },
        { path: '/user',
          name: 'user',
          component: () =>
              import('@/views/user/user'),
          meta: {
            title: '用户管理'
          }
        },
        { path: '/system',
          name: 'system',
          component: () =>
          import('@/views/system/system'),
          children: [
            { path: '/role',
              name: 'role',
              component: () =>
              import('@/views/system/role/role'),
              meta: {
                title: '角色管理'
              }
            },
            { path: '/department',
              name: 'department',
              component: () =>
              import('@/views/system/department/department'),
              meta: {
                title: '部门管理'
              }
            },
            { path: '/dictionaries',
              name: 'dictionaries',
              component: () =>
              import('@/views/system/dictionaries/dictionaries'),
              meta: {
                title: '字典管理'
              }
            },
            { path: '/menu',
              name: 'menu',
              component: () =>
              import('@/views/system/menu/menu'),
              meta: {
                title: '菜单管理'
              }
            },
            { path: '/signIn',
              name: 'signIn',
              component: () =>
              import('@/views/system/signIn/signIn'),
              meta: {
                title: '登录日志'
              }
            },
            { path: '/business',
              name: 'business',
              component: () =>
              import('@/views/system/business/business'),
              meta: {
                title: '业务日志'
              }
            },
            { path: '/monitor',
              name: 'monitor',
              component: () =>
              import('@/views/system/monitor/monitor'),
              meta: {
                title: '监控管理'
              }
            },
            { path: '/notice',
              name: 'notice',
              component: () =>
              import('@/views/system/notice/notice'),
              meta: {
                title: '通知管理'
              }
            }
          ]
        },
        { path: '/joggle',
          name: 'joggle',
          component: () =>
              import('@/views/joggle/joggle'),
          meta: {
            title: '接口文档'
          }
        },
        { path: '/code',
          name: 'code',
          component: () =>
              import('@/views/code/code'),
          meta: {
            title: '代码生成'
          }
        }
      ]
    }
  ]
})



